#navbar{
    background-color:  rgba(28, 43, 75, 1);
}
#school-heading{
    text-align: left;
    font-weight: bold;
    position: relative;
    animation: mymove 20s infinite;
    text-transform: uppercase;
    padding: 10px;
    color: white;
    font-family: cursive;
  }
  @keyframes mymove {
      from {left: 10%;}
      to {left: 80%;}
     
    }
    #main_bg{
        background-color: rgb(2, 4, 24);
       background-image: url("https://timss.bc.edu/img/math-test.jpg") ;
       background-repeat: no-repeat;
       background-position: center;
       padding: 0;
       -webkit-background-size: cover;
       background-size: cover;
       background-attachment: fixed;
     
    }
    #bg{
        background: rgba(38, 57, 88, 0.89);
        min-height: 500px;
    }
    #blank{
        height: 150px;
    }
    #content_heading{
        color: white;
        transition: 0.3s;
    }
    .box{
        font-size: 50px;
        font-weight: 600;
        opacity: 0;
        position: absolute;
        /* width: 100%; */
        color: white;
       text-align: center;
    }
    #text_fade{
        display: flex;
        justify-content: center;
    }
    .b1{
        animation: a1 15s infinite;
    }
    .b2{
        animation: a2 15s infinite;
    }
    .b3{
        animation: a3 15s infinite;
    }
    .b4{
        animation: a4 15s infinite;
    }
    .b5{
        animation: a5 15s infinite;
    }
    @keyframes a1{
        0%{
            opacity: 0;
        }
        10%{
            opacity: 1;
        }
        20%{
            opacity: 0;
        }
        
    }
    @keyframes a2{
        20%{
            opacity: 0;
        }
        30%{
            opacity: 1;
        }
        40%{
            opacity: 0;
        }
        
    }
    @keyframes a3{
       40%{
            opacity: 0;
        }
        50%{
            opacity: 1;
        }
        60%{
            opacity: 0;
        }
        
    }
    @keyframes a4{
        60%{
             opacity: 0;
         }
         70%{
             opacity: 1;
         }
         80%{
             opacity: 0;
         }
         
     }
     @keyframes a5{
        80%{
             opacity: 0;
         }
         90%{
             opacity: 1;
         }
         100%{
             opacity: 0;
         }
         
     }
     #login{
         background-color: white;
         color: rgb(18, 18, 90);
         font-size: 20px;
         font-weight: 900;
